
import { Vue, Component } from 'vue-property-decorator'
import { namespace } from 'vuex-class'
const posts = namespace('posts')

@Component
export default class Home extends Vue {
  mounted() {
    this.getAllPosts()
  }
  @posts.State
  public list!: Array<object>

  @posts.Action
  public getAllPosts!: () => void
}
